// ** React Imports
import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from "react-redux"
import classnames from "classnames"

// ** Custom Components
import UILoader from '@components/ui-loader'
import InputGroup from '@components/input-group'

// ** Third Party Components
import { Button, Col, Form, Row } from 'reactstrap'

// ** Store Actions
import { updateUserCar, userCarEditChange } from '@store/UserCars/actions'

const UserCarAccountTab = ({
  selectedUserCar
}) => {
  const { formErrors, isLoading, isSubmitting } = useSelector(state => state.userCars),
    dispatch = useDispatch()
  // ** Function to handle form submit
  const handleSubmit = event => {
    event.preventDefault()
    dispatch(updateUserCar(selectedUserCar._id))
  }

  const handleFormChange = (name, value) => {
    dispatch(userCarEditChange(name, value))
  }

  return (
    <Row>
      <Col sm='12'>
        <Form onSubmit={handleSubmit}>
          <Row>
            <Col md='4' sm='12'>
              <InputGroup
                type={'text'}
                label={'Car Make'}
                error={formErrors['carMake']}
                name={'carMake'}
                placeholder={'Car Make'}
                onInputChange={(name, value) => {
                  handleFormChange(name, value)
                }}
                defaultValue={selectedUserCar.carMake}
                required={true}
                className={classnames({ 'is-invalid': formErrors['carMake'] })}
              />
            </Col>
            <Col md='4' sm='12'>
              <InputGroup
                type={'text'}
                label={'Car Model'}
                error={formErrors['carModel']}
                name={'carModel'}
                placeholder={'Car Model'}
                onInputChange={(name, value) => {
                  handleFormChange(name, value)
                }}
                defaultValue={selectedUserCar.carModel}
                required={true}
                className={classnames({ 'is-invalid': formErrors['carModel'] })}
              />
            </Col>
            <Col md='4' sm='12'>
              <InputGroup
                type={'text'}
                label={'Engine Capacity'}
                error={formErrors['engineCapacity']}
                name={'engineCapacity'}
                placeholder={'Engine Capacity'}
                onInputChange={(name, value) => {
                  handleFormChange(name, value)
                }}
                defaultValue={selectedUserCar.engineCapacity}
                required={true}
                className={classnames({ 'is-invalid': formErrors['engineCapacity'] })}
              />
            </Col>
            <Col md='4' sm='12'>
              <InputGroup
                type={'text'}
                label={'Registration Number'}
                error={formErrors['registrationNumber']}
                name={'registrationNumber'}
                placeholder={'Registration Number'}
                onInputChange={(name, value) => {
                  handleFormChange(name, value)
                }}
                defaultValue={selectedUserCar.registrationNumber}
                required={true}
                className={classnames({ 'is-invalid': formErrors['registrationNumber'] })}
              />
            </Col>
            <Col md='4' sm='12'>
              <InputGroup
                type={'number'}
                label={'Year Of Manufacture'}
                error={formErrors['yearOfManufacture']}
                name={'yearOfManufacture'}
                placeholder={'Year Of Manufacture'}
                onInputChange={(name, value) => {
                  handleFormChange(name, value)
                }}
                defaultValue={selectedUserCar.yearOfManufacture}
                required={true}
                className={classnames({ 'is-invalid': formErrors['yearOfManufacture'] })}
              />
            </Col>
            <Col md='4' sm='12'>
              <InputGroup
                type={'text'}
                label={'Car Status'}
                error={formErrors['carStatus']}
                name={'carStatus'}
                placeholder={'Car Status'}
                onInputChange={(name, value) => {
                  handleFormChange(name, value)
                }}
                defaultValue={selectedUserCar.carStatus}
                required={true}
                className={classnames({ 'is-invalid': formErrors['carStatus'] })}
              />
            </Col>
            <Col className='d-flex flex-sm-row flex-column mt-2' sm='12'>
              <UILoader blocking={isLoading} className='mr-sm-1'>
                <Button.Ripple className='mb-1 mb-sm-0 mr-0' type='submit' color='primary'>
                  Save Changes
                </Button.Ripple>
              </UILoader>
              <Button.Ripple color='secondary' outline>
                Reset
              </Button.Ripple>
            </Col>
          </Row>
        </Form>
      </Col>
    </Row>
  )
}

export default UserCarAccountTab
