// ** React Imports
import { useEffect, useState } from 'react'
import { Link, useParams } from 'react-router-dom'

// ** UserCars Edit Components
import AccountTab from './Account'

// ** Store & Actions
import { getUserCar } from '@store/UserCars/actions'
import { useDispatch, useSelector } from 'react-redux'

// ** Third Party Components
import { Alert, Card, CardBody, Col, Row } from 'reactstrap'

// ** Custom Components
import UILoader from '@components/ui-loader'
import CarhootBreadCrumbs from '@components/breadcrumbs/CarhootBreadCrumbs'

// ** Styles
import '@styles/react/apps/app-users.scss'

const UserCarEdit = () => {
  // ** States & Vars
  const [activeTab, setActiveTab] = useState('1'),
    dispatch = useDispatch(),
    store = useSelector(state => state.userCars),
    { id, userId } = useParams()
  const { isLoading, selectedUserCar } = store

  // ** Function to toggle tabs
  const toggle = tab => setActiveTab(tab)

  // ** Function to get userCar on mount
  useEffect(() => {
    dispatch(getUserCar(id))
  }, [dispatch])

  return selectedUserCar !== null && selectedUserCar !== undefined ? (
    <UILoader blocking={isLoading}>
      <CarhootBreadCrumbs
        breadCrumbTitle='Breadcrumbs'
        breadCrumbItems={
          [
            {
              link: `/users/view/${selectedUserCar.userId}`,
              title: 'User Car'
            },
            {
              title: 'Edit Car'
            }
          ]
        }
      />
      <Row className='app-user-edit'>
        <Col sm='12'>
          <Card>
            <CardBody className='pt-2'>
              <AccountTab selectedUserCar={selectedUserCar} />
            </CardBody>
          </Card>
        </Col>
      </Row>
    </UILoader>
  ) : (
    <Alert color='danger'>
      <h4 className='alert-heading'>UserCar not found</h4>
      <div className='alert-body'>
        UserCar with id: {id} doesn't exist. Check list of all UserCars: <Link to={`/users/view/${userId}`}>UserCars List</Link>
      </div>
    </Alert>
  )
}

export default UserCarEdit
